import React, { useEffect, useState } from 'react';

import { useParams } from 'react-router-dom';
import shirts from '../../shared/shirts.js';
import './details.css'

import routing from '../../routing.js';

import { Link } from 'react-router-dom'

class ColorButton extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      textColor: compareY(colorNameToHex(this.props.color.toLowerCase())),
      backgroundColor: (compareY(colorNameToHex(this.props.color.toLowerCase()))? this.props.color:'white'),
    }
    this.color = compareY(colorNameToHex(this.props.color.toLowerCase()));
  }

  render () {
    return (
      <button className="coloredBtn tshirtBtn detailBtn flexbox"
        type="button"
        style={{color: this.state.textColor, backgroundColor: this.state.backgroundColor}}
        onClick={() => {this.props.setColor(this.props.color.toLowerCase());}}
        onMouseOver={() => {this.setState({textColor: '#8d8d8d'})}}
        onMouseLeave={() => {this.setState({textColor: this.color})}}
      >
        {this.props.color[0].toUpperCase() + this.props.color.slice(1)}
      </button>
    )
  }
}


const detail = () => {
  let { id } = useParams();
  let tshirt = shirts[id];

  // let [tshirtImage, setTshirtImage] = useState(tshirt.default.front);
  let [tshirtImage, setTshirtImage] = tshirt.colors && tshirt.colors.white && tshirt.colors.white.front ? useState(tshirt.colors.white.front):useState(tshirt.default.front);
  
  
  let [fb, setFb] = useState('front');
  let [color, setColor] = useState('white');
  let [quantity, setQuantity] = useState('1');
  let [size, setSize] = useState('Size:');
  let [linkStyle, setLinkStyle] = useState({
    backgroundColor: "#8d8d8d",
    cursor: "default",
    pointerEvents: "none",
  });
  
  let [cartItem, setCartItem] = useState({
    'id': id,
    'color': color,
    'quantity': quantity,
    'size': size,
  })

  useEffect(() => {
    try {
      setTshirtImage(tshirt.colors[color][fb]);
    } catch {
      setTshirtImage(tshirt.default[fb]);
    }
  }, [fb, color]);

  if (tshirt.price !== undefined) {
    useEffect(() => {
    if (size !== "Size:") {
        setLinkStyle({
  
        })
        return;
      } else {
        setLinkStyle({
          backgroundColor: "#8d8d8d",
          cursor: "default",
          pointerEvents: "none",
        })
      }
    }, [size]);
  }

  useEffect(() => {
    setCartItem({
      ...cartItem,
      'color': color,
      'quantity': quantity,
      'size': size,
    })
  }, [color, quantity, size,])


  const colorList = Object.keys(tshirt.colors).map((color, id) => {
    return (
      <ColorButton key={id} color={color} setColor={setColor}/>
    )
  })

  const quantityList = Array(20).fill().map((_, i) => {
    return (
      <option key={i} value={i+1} className='textMedium'>{i+1}</option>
    )
  })

  const sizeList = [
    "Size:",
    "Women's XS",
    "Women's S",
    "Women's M",
    "Women's L",
    "Women's XL",
    "Women's 2XL",
    "Men's XS",
    "Men's S",
    "Men's M",
    "Men's L",
    "Men's XL",
    "Men's 2XL",
  ].map((size, i) => {
    return (
      <option key={i} value={size} className='textMedium'>{size}</option>
    )
  })

  const changeCnt = new Event('changeCnt');

  return (
    <div className="fullWidth flexbox productContent" style={{backgroundColor: "white"}}>
      <div className="flexbox detailTitle">
        <p className="title">{tshirt.name}</p>
      </div>

      <div className="fullWidth flexbox whiteBackground shirtDetail" style={{backgroundColor: "white"}}>
        <div>
          <img src={tshirtImage} alt="The tshirt based you choosed."/>
        </div>
        <div className="details">
          <div >
            <p className="price">{tshirt.price}</p>
          </div>
          <div className="description">
            <p className="wordWrap">{tshirt.description}</p>
          </div>
          <div className="flexbox select">
            <div>
              <p>Side:</p>
            </div>
            <div className="flexbox">
              <button className="tshirtBtn detailBtn flexbox" type="button" onClick={() => {setFb('front');}}>Front</button>
              <button className="tshirtBtn detailBtn flexbox" type="button" onClick={() => {setFb('back');}}>Back</button>
            </div>
          </div>

          <div className="flexbox select">
            <div>
              <p>Color:</p>
            </div>
            <div className="flexbox">
              {colorList}
            </div>
          </div>

          <div className="flexbox select">
            <div>
              <p>Quantity:</p>
            </div>
            <div>
              <select value={quantity} className="quantityDropdown textMedium" onChange={(e) => {setQuantity(e.target.value);}}>
                {quantityList}
              </select>
            </div>
          </div>

          <div className="flexbox select">
            <div>
              <p>Size:</p>
            </div>
            <div>
              <select value={size} className="quantityDropdown textMedium" onChange={(e) => {setSize(e.target.value);}}>
                {sizeList}
              </select>
            </div>
          </div>

          <div className="flexbox select">
            <div className="flexbox">
              <Link className="tshirtBtn detailBtn flexbox disableBtn"
                type="button"
                style={linkStyle}
                onClick={() => {
                  const cartItems = JSON.parse(localStorage.getItem('cartItems')) || [];
                  cartItems.push(cartItem);
                  localStorage.setItem('cartItems', JSON.stringify(cartItems));
                  console.log("onClick!!");
                  window.dispatchEvent(changeCnt);
                }}
                to={routing.cart}
                exact={true}
              >
                Add To Cart
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

// to={routing.cart}
