export default detail;

const compareY = (hex) => {
  if (hex){
    if (0.299 * Number(`0x${hex.slice(1, 3)}`) + 0.578 * Number(`0x${hex.slice(3, 5)}`) + 0.114 * Number(`0x${hex.slice(5)}`) < 128) {
      return 'white';
    }
  }
  return 'black';
}

